.split-row-select-active {
  background-color: #e6f7ff !important;
  cursor: pointer;
}

.split-row-select {
  cursor: pointer;
}

.ant-table-row {
  background-color: #e6f7ff;
  cursor: pointer;
}

.siteDrawer {
  position: relative;
  overflow: hidden;
}

.container {
  width: 1000px;
  position: absolute; /*Can also be `fixed`*/
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  /*Solves a problem in which the content is being cut when the div is smaller than its' wrapper:*/
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
}

@primary-color: #4e64a2;@border-radius-base: 4px;@layout-header-background: #161c2e;